        />
        <el-select v-model="queryForm.departmentId" placeholder="请选择科室" ref="departmentSelector" default-first-option>
          <el-option label="顶级科室" :value="0" disabled/>
          <el-option v-for="department in departmentList" :key="department.id" :label="department.name"
