            style="--el-switch-on-color: #dcdfe6; --el-switch-off-color: #dcdfe6; margin-left: 10px;"
            active-text="周视图"
            inactive-text="月视图"
            :active-value="true"
            :inactive-value="false"
            width="50px"
            :loading="viewLoading"
        />
      </el-row>
    </el-header>
    <el-main style="padding: 5px; background: aliceblue;" id="mainView">
      <el-scrollbar>
        <el-calendar v-model="queryForm.selectedDate" :range="dateRange" ref="calendar">
          <template #header="{ date }">
            <span>{{ date }}</span>
            <span></span>
            <el-button-group>
              <el-button size="small" @click="handlePreviousWeek" v-if="queryForm.weekViewEnabled">
                上一周
              </el-button>
              <el-button size="small" @click="selectDate('prev-month')" v-else>
                上个月
              </el-button>
              <el-button size="small" @click="selectDate('today')">今天</el-button>
              <el-button size="small" @click="handleNextWeek" v-if="queryForm.weekViewEnabled">
                下一周
              </el-button>
              <el-button size="small" @click="selectDate('next-month')" v-else>
                下个月
              </el-button>
            </el-button-group>
          </template>
          <template #date-cell="{ data }">
            <p :class="data.isSelected ? 'is-selected' : ''">
              {{ data.day.split('-').slice(1).join('-') }}
            </p>
            <div v-if="queryForm.weekViewEnabled">
              <div style="height: 265px; border: 1px solid #95d475; border-radius: 5px;">
                <el-divider>上午</el-divider>
                <el-scrollbar height="210px">
                  <el-card v-for="schedule in getDateData(data.date, 0)" :key="schedule.id"
                           @click.stop="handleEdit(schedule.id,schedule.date,schedule.time)"
                           style="font-size: xx-small">
                    {{ schedule.doctorInfo.name }}
                    <el-tag>{{ schedule.roomInfo.roomNo }}诊室</el-tag>
                  </el-card>
                  <el-card v-if="getDateData(data.date, 0).length<8" @click.stop="handleEdit(null, data.date, 0)">
                    <el-button type="success" :icon="Plus" size="small" circle/>
                  </el-card>
                </el-scrollbar>
              </div>
              <div style="height: 265px; border: 1px solid #95d475; border-radius: 5px;margin-top: 5px;">
                <el-divider>下午</el-divider>
                <el-scrollbar height="210px">
                  <el-card v-for="schedule in getDateData(data.date, 1)" :key="schedule.id"
                           @click.stop="handleEdit(schedule.id,schedule.date,schedule.time)"
                           style="font-size: xx-small">
                    {{ schedule.doctorInfo.name }}
                    <el-tag>{{ schedule.roomInfo.roomNo }}诊室</el-tag>
                  </el-card>
                  <el-card v-if="hasAnyAuthority('business:schedule:add') && getDateData(data.date, 1).length<8" @click.stop="handleEdit(null, data.date, 1)">
                    <el-button type="success" :icon="Plus" size="small" circle/>
                  </el-card>
                </el-scrollbar>
              </div>
            </div>
            <div v-else>
              <el-badge :value="getDateData(data.date, null).length" :hidden="getDateData(data.date, null).length<1">
                <el-button @click="queryForm.selectedDate = data.date; queryForm.weekViewEnabled = true">查看详情
                </el-button>
              </el-badge>
            </div>
          </template>
        </el-calendar>
      </el-scrollbar>
    </el-main>
  </el-container>
  <EditDialog v-model:edit-dialog-visible="editDialogVisible" :edit-props="editProps" :title="editTitle"
              @handleSubmit="fetchData"/>
