const departmentList = ref([]);

// 定义日历显示的起止日期
const dateRange = ref(null);

// 定义视图是否正在切换
const viewLoading = ref(false);

// 定义日历组件的引用
const calendar = ref()

// 定义科室选择器的引用
const departmentSelector = ref(null);

// 定义编辑对话框的显示状态
const editDialogVisible = ref(false);

// 定义传入编辑对话框的排班属性
const editProps = ref({
  id: -1,
  date: new Date(),
  time: 0,
  departmentId: 0,
});

// 定义传入编辑对话框的title值
const editTitle = ref('');
/*====以下为各按钮的点击事件====*/

// 处理切换上一周视图
const handlePreviousWeek = () => {
  queryForm.selectedDate = new Date(queryForm.selectedDate.getTime() - 1000 * 60 * 60 * 24 * 7);
}

// 处理切换下一周视图
const handleNextWeek = () => {
  queryForm.selectedDate = new Date(queryForm.selectedDate.getTime() + 1000 * 60 * 60 * 24 * 7);
}

// 处理点击上个月、今天、下个月
const selectDate = (val) => {
  calendar.value.selectDate(val)
}

// 处理新增、编辑
const handleEdit = (id, date, time) => {
  console.log('点击', id, date, time);
  if (id) {
    console.log('修改...', id);
    editProps.value = {
      id: id,
      date: date,
      time: time,
      departmentId: queryForm.departmentId,
    }
    editTitle.value = '排班修改';
  } else {
    console.log('新增...', id);
    editProps.value = {
      id: -1,
      date: date,
      time: time,
      departmentId: queryForm.departmentId,
    }
    editTitle.value = '排班添加';
  }
  editDialogVisible.value = true;
}

/*====以下为一些监听以及其他函数====*/

// 获取指定日期、班次的排班
const getDateData = (date, time) => {
  let curYear = date.getFullYear();
  let curMonth = date.getMonth();
  let curDate = date.getDate();
  let dateData = [];
  for (const schedule of scheduleData.value) {
    if (schedule.date.getFullYear() == curYear && schedule.date.getMonth() == curMonth && schedule.date.getDate() == curDate && (time == null || schedule.time == time)) {
      dateData.push(schedule);
    }
  }
  return dateData;
}

// 转换日期为字符串
const dateToStr = (dateDate) => {
  let year = dateDate.getFullYear();
  let month = (dateDate.getMonth() + 1);
  let date = dateDate.getDate();
  let dateStr = year + '-' + (month < 10 ? '0' + month : month) + '-' + (date < 10 ? '0' + date : date);
  console.log('dateToStr dateDate=', dateDate, ', dateStr=', dateStr);
  return dateStr;
}

// 转换字符串为日期
const strToDate = (dateStr) => {
  let year = parseInt(dateStr.substring(0, 4));
  let month = parseInt(dateStr.substring(5, 7));
  let date = parseInt(dateStr.substring(8, 10));
  // console.log(year, month, date);
  let dateDate = new Date(year, month - 1, date, 0, 0, 0, 0);
  console.log('strToDate dateStr=', dateStr, ', dateDate=', dateDate);
  return dateDate;
}

// 从后台获取数据
const fetchData = async () => {
  // 转换日期为字符串
  let dateStr = dateToStr(queryForm.selectedDate);
  let realQueryForm = {
    departmentId: queryForm.departmentId,
    weekViewEnabled: queryForm.weekViewEnabled,
    selectedDate: dateStr,
    // selectedDate: queryForm.selectedDate.getFullYear() + '-' + (queryForm.selectedDate.getMonth() + 1) + '-' + queryForm.selectedDate.getDate(),
  }
  let url = '/api/bsns/schedules?' + qs.stringify(realQueryForm);
  console.log('url=', url);
  // 从后台获取数据
  let res = await AxiosUtil.get(url);
  console.log("更新排班列表 res= ", res);
  if (res.data.code == 20010) {
    // 转换字符串为日期
    let scheduleList = [];
    res.data.scheduleList.forEach(schedule => {
      schedule.date = strToDate(schedule.date);
      scheduleList.push(schedule);
    });
    // 更新日历数据
    scheduleData.value = scheduleList;
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
  // 后台请求科室列表
  let res2 = await AxiosUtil.get('/api/bsns/departments');
  console.log(res2.data);
  if (res2.data.code == 20010) {
    let departments = [];
    res2.data.departmentList.forEach(item => {
      item.name = "+ " + item.name;
      departments.push(item);
      item.children.forEach(child => {
        child.name = "••• " + child.name;
        departments.push(child);
      })
    });
    // 更新科室列表
    departmentList.value = departments;
  }
}

fetchData();

// 当选定日期变化时，重新计算日历的起止日期
watch(queryForm, (newValue, oldValue) => {
  console.log('watch queryForm.selectedDate=', newValue.selectedDate);
  if (queryForm.weekViewEnabled) {
    let start = new Date(newValue.selectedDate.getTime() - 1000 * 60 * 60 * 24 * newValue.selectedDate.getDay());
    let end = new Date(newValue.selectedDate.getTime() + 1000 * 60 * 60 * 24 * (6 - newValue.selectedDate.getDay()));
    // 更新日历的起止日期
    dateRange.value = [start, end];
  } else {
    // 取消绑定日历的起止日期
    dateRange.value = null;
  }
  console.log(Date.now() + ' 重新拉取数据...');
  console.log(queryForm);
  fetchData();
}, {deep: true});

