<template>
  <el-dialog v-model="editDialogVisible" :title="title" :before-close="handleClose" width="600px" draggable>
    <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        inline
        label-width="80px"
        label-position="right"
        status-icon>
      <el-form-item label="科室" prop="departmentId" style="width: 48%;">
        <el-select v-model="editForm.departmentId" placeholder="请选择科室" disabled>
          <el-option label="顶级科室" :value="0"/>
          <el-option v-for="department in departmentList" :key="department.id" :label="department.name"
                     :value="department.id"/>
        </el-select>
      </el-form-item>
      <el-form-item label="诊室" prop="roomId" style="width: 40%;">
        <el-select v-model="editForm.roomId" placeholder="请选择诊室">
          <el-option v-for="room in roomList" :key="room.id" :label="room.roomNo+' . '+room.note"
                     :value="room.id"/>
        </el-select>
      </el-form-item>
      <el-form-item label="日期" prop="date" style="width: 48%;">
        <el-date-picker
            v-model="editForm.date"
            type="date"
            placeholder="选择日期"
            :clearable="false"
        />
      </el-form-item>
      <el-form-item label="班次" prop="time" style="width: 40%;">
        <el-radio-group v-model="editForm.time">
          <el-radio :label="0" size="large">上午</el-radio>
          <el-radio :label="1" size="large">下午</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="值班人员" prop="workerId" style="width: 100%;">
        <el-select
            v-model="editForm.workerId"
            filterable
            default-first-option
            placeholder="搜索人员"
            style="width: 100%;"
        >
          <el-option
              v-for="worker in workerList"
              :key="worker.id"
              :label="worker.doctorInfo.name + ' . ' + (worker.doctorInfo.sex == '0' ? '男' : '女') + ' . ' + worker.doctorInfo.tel + ' . ' + worker.doctorInfo.idNo"
              :value="worker.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="接纳人数" prop="capacity" style="width: 48%;">
        <el-input v-model="editForm.capacity" type="number"/>
      </el-form-item>
<!--      <el-form-item label="状态" prop="disabled" style="width: 40%;">-->
<!--        <el-switch-->
<!--            v-model="editForm.disabled"-->
<!--            style="&#45;&#45;el-switch-on-color: #13ce66; &#45;&#45;el-switch-off-color: #ff4949"-->
<!--            active-text="启用"-->
<!--            inactive-text="禁用"-->
<!--            :active-value="false"-->
<!--            :inactive-value="true"-->
<!--            width="50px"-->
<!--            disabled-->
<!--        />-->
<!--      </el-form-item>-->
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-popconfirm title="您确定要删除这条记录吗？" @confirm="handleDelete(editProps.id)"
                       v-if="hasAnyAuthority('business:schedule:delete') && editProps.id!=-1">
          <template #reference>
            <el-button type="danger" :icon="Delete"/>
          </template>
        </el-popconfirm>
        <el-button @click="handleReset(editProps)">重置</el-button>
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleConfirm">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script setup>
import {defineProps, defineEmits, computed, ref, watch, reactive} from 'vue';
import AxiosUtil from "@/utils/AxiosUtil";
import {ElMessageBox, ElMessage} from "element-plus";
import {Delete} from '@element-plus/icons-vue'

// 定义传入该子组件的参数
const props = defineProps({
  editDialogVisible: {
    type: Boolean,
    required: true,
    default: false
  },
  editProps: {
    type: Object,
    required: true,
    default: () => {
      return {
        id: -1, // 排班id
        date: new Date(), // 日期
        time: 0, // 班次
        departmentId: 0, //科室id
      }
    }
  },
  title: {
    type: String,
    required: true,
    default: ''
  },
});

// 定义组件（自定义）事件
const emit = defineEmits(['update:editDialogVisible', 'handleSubmit']);

// 定义“编辑”对话框的显示状态
const editDialogVisible = computed({
  // 只读！读取时从父组件获取，更新时更新父组件
  get() {
    return props.editDialogVisible;
  },
  set(newValue) {
    emit('update:editDialogVisible', newValue);
  }
});

// 定义表单数据
const editForm = ref({
  id: -1,
  date: '',
  time: 0,
  departmentId: 0,
  roomId: '',
  workerId: '',
  capacity: 25,
  disabled: false,
});

// 定义科室列表
const departmentList = ref([]);

// 定义诊室列表
const roomList = ref([]);

// 定义岗位列表
const workerList = ref([]);

// 转换日期为字符串
const dateToStr = (dateDate) => {
  let year = dateDate.getFullYear();
  let month = (dateDate.getMonth() + 1);
  let date = dateDate.getDate();
  let dateStr = year + '-' + (month < 10 ? '0' + month : month) + '-' + (date < 10 ? '0' + date : date);
  console.log('dateToStr dateDate=', dateDate, ', dateStr=', dateStr);
  return dateStr;
}

// 转换字符串为日期
const strToDate = (dateStr) => {
  let year = parseInt(dateStr.substring(0, 4));
  let month = parseInt(dateStr.substring(5, 7));
  let date = parseInt(dateStr.substring(8, 10));
  // console.log(year, month, date);
  let dateDate = new Date(year, month - 1, date, 0, 0, 0, 0);
  console.log('strToDate dateStr=', dateStr, ', dateDate=', dateDate);
  return dateDate;
}

// 定义表单校验规则
const validateSchedule = async (rule, value, callback) => {
  // console.log(rule.field)
  // console.log(value)

  let url, h, date;
  // 转换日期为字符串
  let dateStr = dateToStr(editForm.value.date);
  // 基础格式验证通过后，才给 url 赋值。
  switch (rule.field) {
    case 'date':
      console.log('date=', value);
      if (editForm.value.time == 0) {
        h = 8;
      } else {
        h = 14;
      }
      date = new Date(value.getFullYear(), value.getMonth(), value.getDate(), h, 0, 0, 0);
      if (date.getTime() < new Date().getTime()) {
        callback(new Error('所选日期不得早于当前日期'));
      } else {
        url = '/api/bsns/schedules/checkSchedule?id=' + editForm.value.id + '&date=' + dateStr + '&time=' + editForm.value.time + '&workerId=' + editForm.value.workerId;
      }
      break;
    default:
      console.log('value=', value);
      break;
  }
  // url 有值，才向后台发送验证请求。
  // console.log(url);
  if (url) {
    // console.log('向后台发送请求');
    let res = await AxiosUtil.post(url);
    // console.log(res.data);
    if (res.data.code == 1) {
      callback(new Error(res.data.msg));
    } else {
      callback();
    }
  }
}

// 定义表单验证规则
const editFormRules = reactive({
  date: [
    {required: true, message: '日期不能为空', trigger: 'blur'},
    {validator: validateSchedule, trigger: 'change'},
    {validator: validateSchedule, trigger: 'blur'},
  ],
  time: [
    {required: true, message: '班次不能为空', trigger: 'blur'},
  ],
  roomId: [
    {required: true, message: '诊室不能为空', trigger: 'blur'},
  ],
  workerId: [
    {required: true, message: '值班人员不能为空', trigger: 'blur'},
  ],
  capacity: [
    {required: true, message: '接纳人数不能为空', trigger: 'blur'},
  ],
  disabled: [
    {required: true, message: '状态不能为空', trigger: 'blur'},
  ],
})

// 定义表单引用
const editFormRef = ref(null);

// 表单重置事件
const handleReset = async (editProps) => {
  if (editProps.id == -1) {
    editForm.value = {
      id: -1,
      date: editProps.date,
      time: editProps.time,
      departmentId: editProps.departmentId,
      roomId: '',
      workerId: '',
      capacity: 25,
      disabled: false,
    };
    console.log('清空表单...');
  } else {
    let res = await AxiosUtil.get('/api/bsns/schedules/' + editProps.id);
    console.log(res.data);
    if (res.data.code == 20010) {
      let schedule = res.data.schedule;
      // 转换字符串为日期
      schedule.date = strToDate(schedule.date);
      // 更新表格数据
      editForm.value = schedule;
    } else {
      ElMessage({
        message: res.data.msg,
        type: 'error',
      });
    }
    console.log('从后台填充...');
  }
  // 向后台请求科室列表
  let res1 = await AxiosUtil.get('/api/bsns/departments');
  console.log(res1.data);
  if (res1.data.code == 20010) {
    let departments = [];
    res1.data.departmentList.forEach(item => {
      item.name = "+ " + item.name;
      departments.push(item);
      item.children.forEach(child => {
        child.name = "••• " + child.name;
        departments.push(child);
      })
    });
    // 更新科室列表
    departmentList.value = departments;
  } else {
    ElMessage({
      message: res1.data.msg,
      type: 'error',
    });
    // 清空科室列表
    departmentList.value = [];
  }
  // 向后台请求诊室列表
  let res2 = await AxiosUtil.get('/api/bsns/rooms/getByDepartmentId/' + editForm.value.departmentId);
  console.log(res2.data);
  if (res2.data.code == 20010) {
    // 更新诊室列表
    roomList.value = res2.data.roomList;
  } else {
    ElMessage({
      message: res2.data.msg,
      type: 'error',
    });
    // 清空诊室列表
    roomList.value = [];
  }
  // 向后台请求岗位列表
  let res3 = await AxiosUtil.get('/api/bsns/workers/getByDepartmentId/' + editForm.value.departmentId);
  console.log(res3.data);
  if (res3.data.code == 20010) {
    // 更新岗位列表
    workerList.value = res3.data.workerList;
  } else {
    ElMessage({
      message: res3.data.msg,
      type: 'error',
    });
    // 清空岗位列表
    workerList.value = [];
  }
}

// 定义关闭对话框前的确认操作
const handleClose = (done) => {
  ElMessageBox.confirm('确认在未保存的情况下关闭对话框？')
      .then(() => {
        done()
      })
      .catch(() => {
        // catch error
      })
}

// 定义对话框关闭事件
const handleCancel = () => {
  editDialogVisible.value = false;
}

// 定义确认按钮事件
const handleConfirm = () => {
  // 表单验证
  editFormRef.value.validate(async valid => {
    if (valid) {
      // 表单验证通过
      let url, res;
      // 转换日期为字符串
      let dateStr = dateToStr(editForm.value.date);
      let realEditForm = {
        id: editForm.value.id,
        date: dateStr,
        time: editForm.value.time,
        departmentId: editForm.value.departmentId,
        roomId: editForm.value.roomId,
        workerId: editForm.value.workerId,
        capacity: editForm.value.capacity,
        disabled: editForm.value.disabled,
      }
      if (editForm.value.id == -1) {
        console.log('提交新增...');
        url = '/api/bsns/schedules';
        res = await AxiosUtil.post(url, realEditForm);
        console.log("新增排班 res.data= ", res.data);
      } else {
        console.log('提交修改...');
        url = '/api/bsns/schedules';
        res = await AxiosUtil.put(url, realEditForm);
        console.log("修改排班 res.data= ", res.data);
      }
      if (res.data.code == 20020 || res.data.code == 20030) {
        ElMessage({
          message: res.data.msg,
          type: 'success',
        });
        emit('handleSubmit'); // 触发自定义事件，使父组件刷新数据
        handleCancel(); // 关闭对话框
      } else {
        ElMessage({
          message: res.data.msg,
          type: 'error',
        });
      }
    }
  });
}

// 处理删除记录
const handleDelete = async (id) => {
  id = [id];
  console.log('删除...', id);
  let url = '/api/bsns/schedules/' + id.join(',');
  console.log('url=', url);
  let res = await AxiosUtil.del(url);
  console.log('res=', res);
  if (res.data.code == 20040) {
    ElMessage({
      message: res.data.msg,
      type: 'success',
    });
    emit('handleSubmit'); // 触发自定义事件，使父组件刷新数据
    handleCancel();
  } else {
    ElMessage({
      message: res.data.msg,
      type: 'error',
    });
  }
}

// 当对话框重新显示时重置表单
watch(editDialogVisible, (newValue) => {
  if (newValue == true) {
    handleReset(props.editProps);
  }
})

</script>


<script>
export default {
  name: "EditDialog"
}
</script>

<style scoped>

</style>